<template>
    <label
        class="title"
    >
        <span class="title-text" v-if="props.title">{{ props.title?.text || t(props.title) }}</span>

        <template v-for="(item,id) in props.items" :key="item.id">
            <Button
                :name="t(item.name)"
                class="size-s secondary"
                @click="onClick(item)"
            />
        </template>
    </label>
</template>

<script setup>
    import { ref, watch } from 'vue';
    import { useI18n } from 'vue-i18n';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import stores from '@/stores';

    const locale = stores.locale();
    const nav = stores.nav();

    const { t } = useI18n();

    const props = defineProps({
        title: { type: [Object, String, null], default: null },
        items: { type: Array, default: () => [] }
    });

    const onClick = (item) => {
        if (item.page) {
            nav.open(item.page, item.params);
        }
    };
</script>

<style lang="scss" scoped>
    .title {
        width: 100%;
        overflow-x: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 8px;
        justify-content: space-between;
        white-space: nowrap;

        &::-webkit-scrollbar {
            display: none;
        }
            
        .title-text {
            font: var(--font-h6);
            font-weight: bold;
            color: var(--dark-500);
            flex-shrink: 0;
            margin-right: 12px;
            white-space: normal;
            max-width: 100%;
            flex: 1;
        }

        button {
            flex-shrink: 0;
        }
    }
</style>
